import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"
import { arrSortByProps } from '@/utils'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    answer: {},
    company: {},
    invoice: [],
    confirmSign: false
  },
  getters: {
    deadDate(state) {
      let m = parseInt(state.company.month.substr(-2, 2))
      let next_y = parseInt(state.company.month.substr(0, 4) )
      let next_m = ''
      if (m === 12) {
        next_m = 1
        next_y += 1
      } else {
        next_m = m + 1
      }
      if (next_m.toString().length < 2) {
        next_m = '0' + next_m
      }
      return next_y + '-' + next_m + '-16'
    }
  },
  //持久化插件使用
  // plugins: [createPersistedState({
  //   key: 'renzheng',
  //   storage: window.sessionStorage,
  // })],
  mutations: {
    setCompany(state, data) {
      state.company = data
    },
    setInvoice(state, data) {
      state.invoice = data
    },
    resetAnswer(state) {
      state.answer = {}
    },
    // 发票勾选
    checkInvoice(state, data) {
      state.invoice.forEach(v => {
        if (data.indexOf(v) > -1) {
          v.check_state = '1'
          v.check_time = state.company.month + '-30'
        }
      })
      // 保存答案
      if (!state.answer.invoice) {
        state.answer.invoice = []
      }
      data.forEach(v => {
        state.answer.invoice.push(v)
      })
      state.answer.invoice.sort(arrSortByProps('number'))
    },
    // 取消勾选
    unCheckInvoice(state, data) {
      state.invoice.forEach(v => {
        if (data.indexOf(v) > -1) {
          v.check_state = '0'
          v.check_time = ''
        }
      })
      // 保存答案
      state.answer.invoice.forEach(v => {
        if (data.indexOf(v) > -1) {
          v.check_state = '0'
          v.check_time = ''
        }
      })
    },
    // 勾选不抵扣发票
    checkDeductionInvoice(state, data) {
      state.invoice.forEach(v => {
        if (data.indexOf(v) > -1) {
          v.check_state = '1'
          v.deduction = 1
          v.check_time = state.company.month + '-30'
        }
      })
      // 保存答案
      state.answer.invoice.forEach(v => {
        if (data.indexOf(v) > -1) {
          v.check_state = '1'
          v.deduction = 1
          v.check_time = state.company.month + '-30'
        }
      })
    },
    // 取消不抵扣勾选
    unCheckDeductionInvoice(state, data) {
      state.invoice.forEach(v => {
        if (data.indexOf(v) > -1) {
          v.check_state = '0'
          v.check_time = ''
          delete v.deduction
        }
      })
      // 保存答案
      state.answer.invoice.forEach(v => {
        if (data.indexOf(v) > -1) {
          v.check_state = '0'
          v.check_time = ''
          delete v.deduction
        }
      })
    },
    // 确认签名
    confirmSign(state) {
      state.confirmSign = true
      state.answer.confirmSign = true
    },
    // 重置密码
    setting(state, data) {
      state.company = Object.assign(state.company, data)
      state.answer.company = Object.assign(state.company, data)
    }
  },
})

export default store



