<template>
  <div class="frame">
    <el-container style="overflow:auto;">
      <el-header style="height: 110px;">
        <!--头部-->
        <div class="bg-f login-head">
          <div class="width-1200 relative">
            <div class="f-l">
              <img src="../assets/images/logo2.gif">
            </div>
            <div class="f-r ribbon">
              <a href="javascript:void(0);" title="流程指引" class="lczy notice1">
                <span>流程指引</span>
              </a>
              <a href="javascript:void(0);" title="检测环境" class="jchj notice1">
                <span>检测环境</span>
              </a>
              <a href="javascript:void(0);" title="升级公告" class="sjgg notice1">
                <span>历史公告</span>
              </a>
              <a href="javascript:void(0);" title="常见问题" class="cjwt notice1">
                <span>常见问题</span>
              </a>
              <a href="javascript:void(0);" title="操作手册" class="czsc notice1">
                <span>操作手册</span>
              </a>
            </div>
            <div class="clear"></div>
          </div>
        </div>
      </el-header>
      <el-container style="min-width: 1250px;">
        <el-main>
          <!--中间-->
          <div class="login-cont">
            <div class="width-1200 relative">
              <img src="../assets/images/new_login_pic.png" class="bj2">
              <div class="login_box">
                <div class="mbbox">
                  <ul>
                    <li id="mb2" class="font-18 hover">系统登录</li>
                  </ul>
                </div>
                <div class="conbox">
                  <div id="con_mb_2" style="padding: 0 30px;">
                    <div class="pd-vertical-10">
                      <div class="login_input">
                        <img class="login-key-icon" src="../assets/images/login_key_icon.gif">
                        <el-input v-model="certifyPwd" show-password :clearable="true" style="height: 48px !important;"
                                  placeholder="输入金税盘、税控盘或税务Ukey证书密码"></el-input>
                      </div>
                    </div>
                    <div v-if="company.enablePlatPwd">
                      <div class="pd-vertical-10">
                        <div class="login_input">
                          <span>平台密码：</span>
                          <el-input type="password" prefix-icon="iconfont new_iconupan" v-model="platPwd"
                                    placeholder="输入平台密码"></el-input>
                        </div>
                      </div>
                      <div class="f-r mg-right-5">
                        <a href="#" class="for_key for_key_alert">忘记平台密码？</a>
                      </div>
                      <div class="clear"></div>
                    </div>
                    <div class="pd-vertical-10">
                      <el-button type="primary" class="sub_1" @click="toLogin">登录</el-button>
                    </div>
                    <div>默认登录密码为：12345678</div>
                  </div>
                  <div class="login-info-foot">
                    <div class="driver_div">首次访问请下载驱动程序和应用客户端</div>
                    <div class="fav_div">欢迎收藏本站</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </el-main>
        <el-footer>
          <div class="width-1200">
            本系统为教学系统
          </div>
        </el-footer>
      </el-container>
    </el-container>
  </div>
</template>

<script>
  export default {
    name: 'Index',
    data () {
      return {
        platPwd: '', // 平台密码
        certifyPwd: '' // 金税盘密码
      }
    },
    computed: {
      company() {
        return this.$store.state.company
      },
    },
    created () {
      // 单独运行的时候开启
      // if (Object.keys(this.company).length === 0) {
      //   this.initData()
      // }
    },
    methods: {
      initData () {
        const company = {
            certify_pwd: '1',  // 证书密码
            plat_pwd: '', //平台密码
            enablePlatPwd: 0, //是否开启平台密码
            company: '山东问云机械有限公司',
            level: 'B',
            old_no: '91370305755666003U',
            province: '山东',
            sub_date: '月',
            tax_no: '91370305755666003U',
            month: '2020-06'
        }
        const invoice = [
          {
            certify_state: '0',
            certify_time: null,
            check_state: '0',  // 是否勾选
            check_time: null, // 勾选时间
            tax_cate: '1',  // 增值税专用发票 还是机动车销售统一发票
            state: '1',  // 发票状态 0作废  1 正常 2异常 3失控  4 红冲 默认1就行
            scan_state: '0',
            scan_time: null,

            create_time: '2020-06-12', // 开票日期
            code: '3700171130',  //发票代码
            number: '32544580', // 发票号码
            money: '120000.00', //金额
            declare_tax_money: '19200.00',  //有效抵扣税额
            tax_money: '19200.00',  // 税额
            saler_name: '淄博天成塑业有限公司',
            saler_tax_no: '91370305755362038U'
          },
          {
            certify_state: '0',
            certify_time: null,
            check_state: '0',  // 是否勾选
            check_time: null, // 勾选时间
            tax_cate: '1',  // 增值税专用发票 还是机动车销售统一发票
            state: '1',  // 发票状态 0作废  1 正常 2异常 3失控  4 红冲 默认1就行
            scan_state: '0',
            scan_time: null,

            create_time: '2020-06-15', // 开票日期
            code: '3700171130',  //发票代码
            number: '32544581', // 发票号码
            money: '16500.00', //金额
            declare_tax_money: '200.00',  //有效抵扣税额
            tax_money: '200.00',  // 税额
            saler_name: '淄博希望农业科技有限公司',
            saler_tax_no: '91370334575536208U'
          }
        ]
        // 同步企业基本信息
        this.$store.commit('setCompany', company)
        this.$store.commit('setInvoice', invoice)
      },
      toLogin () {
        if (this.certifyPwd !== this.$store.state.company.certify_pwd) {
          this.$message({
            message: '证书密码错误',
            type: 'warning'
          })
          return false
        }
        if (this.company.enablePlatPwd && this.company.plat_pwd !== this.platPwd)　{
          this.$message({
            message: '平台密码错误',
            type: 'warning'
          })
          return false
        }
        this.$router.push('/home/indexcertificate')
      }
    }
  }
</script>

<style>
  .login_input .el-input__inner {
    padding: 0 0 0 45px !important;
  }
</style>
<style scoped>
  .login-key-icon {
    position: absolute;
    z-index: 2;
    top: 113px;
    left: 45px;
  }

  .driver_div {
    background: url("../assets/images/index_icon_download.png") left center no-repeat;
    padding-left: 40px;
  }

  .fav_div {
    background: url("../assets/images/index_icon_fav.png") left center no-repeat;
    padding-left: 40px;
  }

  .login-info-foot {
    margin-top: 20px;
    font-size: 12px;
    line-height: 30px;
    background: #f4f4f4;
    border-top: 1px solid #e3e3e3;
    padding: 10px 30px;
    border-radius: 0 0 2px 2px;
    text-align: left;
  }

  .bj2 {
    position: absolute;
    left: 9%;
    top: 45px;
  }

  .mr-5 {
    margin-right: 5px;
  }

  .mt-10 {
    margin-bottom: 10px;
  }

  .frame {
    width: 100%;
    height: 100%;
  }

  .float-right {
    float: right;
  }

  .el-header {
    color: #333;
    text-align: center;
    line-height: 30px;
  }

  .el-footer {
    padding: 30px;
    color: #333;
    text-align: center;
    /*line-height: 15px;*/
  }

  .el-aside {
    background-color: #D3DCE6;
    color: #333;
    text-align: center;
    line-height: 200px;
  }

  .el-main {
    color: #333;
    text-align: center;
    padding: 0px;
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  .el-container:nth-child(5) .el-aside,
  .el-container:nth-child(6) .el-aside {
    line-height: 260px;
  }

  .el-container:nth-child(7) .el-aside {
    line-height: 320px;
  }

  .login-cont {
    background: url("../assets/images/new_login_bg.gif") no-repeat;
    background-size: cover;
  }

  .do-next {
    background: url("../assets/images/do_next.png");
  }
</style>



