<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App',
    methods: {
      initData () {
        const company = {
          certify_pwd: '12345678',
          plat_pwd: '', //平台密码
          enablePlatPwd: 0, //是否开启平台密码
          company: 'test',
          level: 'B',
          old_no: '123123',
          province: 'xx',
          sub_date: '月',
          tax_no:  '123123123123',
          month: '12'
        }
        // 同步企业基本信息
        this.$store.commit('setCompany', company)
        const data = [
          { code: "3700171130",
            create_time: "2019-12-4",
            declare_tax_money: "15600",
            money: "120000",
            number: "32544580",
            purpose: "抵扣",
            saler_name: "淄博天成塑业有限公司",
            saler_tax_no: "91370305755362038U",
            tax_money: "15600",
            certify_state: '0',
            certify_time: null,
            check_state: '0',  // 是否勾选
            check_time: null, // 勾选时间
            tax_cate: '1',  // 增值税专用发票 还是机动车销售统一发票
            state: '1',  // 发票状态 0作废  1 正常 2异常 3失控  4 红冲 默认1就行
            scan_state: '0',
            scan_time: null
          },
          { code: "3700171130",
            create_time: "2019-12-5",
            declare_tax_money: "15847",
            money: "121900",
            number: "35650560",
            purpose: "抵扣",
            saler_name: "济南百全管材制造有限公司",
            saler_tax_no: "913123565000254000",
            tax_money: "15847",
            certify_state: '0',
            certify_time: null,
            check_state: '0',  // 是否勾选
            check_time: null, // 勾选时间
            tax_cate: '1',  // 增值税专用发票 还是机动车销售统一发票
            state: '1',  // 发票状态 0作废  1 正常 2异常 3失控  4 红冲 默认1就行
            scan_state: '0',
            scan_time: null
          }
        ]
        this.$store.commit('setInvoice', data)
      },
      sendMessage (cmd, params) {
        window.parent.postMessage({ cmd, params }, '*')
      },
      handleMessage (msg) {
        const { data } = msg
        if (data.cmd === 'changeMonth') {
          const company = Object.assign(this.$store.state.preset.companyInfo.company, {
            month: data.params
          })
          this.$store.commit('setCompany', company)
        }
        if (data.cmd === 'preset') { // 预设的企业信息
          const company = {
              certify_pwd: '12345678',
              plat_pwd: '', //平台密码
              enablePlatPwd: 0, //是否开启平台密码
              company: data.params['company_title'],
              level: 'B',
              old_no: data.params['tax_id'],
              province: 'xx',
              sub_date: '月',
              tax_no:  data.params['tax_id'],
              month: data.params['month']
          }
          // 同步企业基本信息
          this.$store.commit('setCompany', company)
          // 获取预设发票
          this.sendMessage('getInvoice')
        }
        if (data.cmd === 'presetInvoice') { // 同步发票信息
          if (Array.isArray(data.params) && data.params.length > 0){
            for(let i = 0; i < data.params.length; i++) {
              data.params[i] = Object.assign(data.params[i], {
                certify_state: '0',
                certify_time: null,
                check_state: '0',  // 是否勾选
                check_time: null, // 勾选时间
                tax_cate: '1',  // 增值税专用发票 还是机动车销售统一发票
                state: '1',  // 发票状态 0作废  1 正常 2异常 3失控  4 红冲 默认1就行
                scan_state: '0',
                scan_time: null,
              })
            }
          }
          this.$store.commit('setInvoice', data.params)
        }
        if (data.cmd === 'getAnswer') {
          const answer = this.$store.state.answer
          let keys = Object.keys(answer)
          console.log(keys)
          if (keys.length > 0) {
            this.sendMessage('echo', { business_id: data.params['business_id'], answer: answer })
            this.$store.commit('resetAnswer')
          }
        }
      }
    },
    mounted () {
      this.$nextTick(() => {
        window.addEventListener('message', this.handleMessage)
        console.log('认证系统注册消息成功')
        // this.initData()
      })
    },
    destroyed () {
      window.removeEventListener('message', this.handleMessage)
    }
  }
</script>

<style>
  html, body, #app {
    margin: 0px;
    padding: 0px;
    height: 100%;
    width: 100%;
    min-width: 1200px;
    overflow: auto;
    display: flex;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    color: #2c3e50;
  }
</style>



